@import url(https://fonts.googleapis.com/css2?family=Cabin:ital@1&family=Roboto+Mono:wght@100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}




.App {
  text-align: center;
}



@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}


@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.header {
  text-align: center;
  color: #fff;
  margin-bottom: 50px;
}

.redC {
  border: 1px solid red;
}

.parentDiv {
  background: url(/static/media/signupbg.ee09e65e.png) no-repeat center center;
  background-size: cover;
  min-width: 100vw;
  min-height: 100vh;
}

.formContainer {
  background-color: #fff;
  width: 40%;
  margin: 0 auto;
  border-radius: 6px;
  padding: 30px 25px;
}

.btn-signup {
  background-color: #230480;
	color: white;
}

.btn-signup:hover {
  color: #fff;
  background-color: #3b1e94;
}
.loginParentDiv {
	background: url(/static/media/loginBG.e23f6f9e.png) no-repeat center center;
	background-size: cover;
	min-width: 100vw;
	min-height: 100vh;
}

.loginContainerDiv {
	padding-top: 200px;
}

.loginFormDiv {
	background-color: #fff;
	width: 100%;
	margin: 0 auto;
	border-radius: 6px;
	padding: 30px 15px;
}

.btn-flat {
	background-color: #230480;
	color: white;
}

.btn-flat:hover {
  background-color: #3b1e94;
  color: #fff;
}


.redC {
	border: 1px solid red;
}

.getStarted {
  width: 120px;
  height: 43px;
  background:  #3b1e94;
  border-radius: 6px;
  text-decoration: none;
  text-align: center;
  line-height: 39px;
  margin: 0 auto;
}

.getStart {
  color: #ffffff;
  text-decoration: none;
}

.getStart:hover {
  text-decoration: none;
  color: #ffffff;
}

.nav-item, .nav-item:hover, .nav-item:active {
  background-color: #3b1e94 !important;
  color: #ffffff;
  margin-left: 5px;
}

.nav-link, .nav-link:hover, .nav-link:active {
  background-color: #3b1e94;
  margin-left: 5px;
  color: #ffffff;
}

.historyTable {
	text-align: center;
	width: 100%;
  border-collapse: separate;
  border-spacing: 0 19px;
}


.historyTable thead tr th {
	border-bottom: 2.4px solid #3b1e94;
  font-size: 18px;
}

.historyTable th,
.historyTable td {
	padding: 0 5rem 0 4rem;
}
.rangeSlid {
  color:  #230480;
  background-color:  #230480;
}

.loanDetailsMember {
  display: flex;
  justify-content: space-between;
}

.loanDetailsMember h5 {
  color: #230480; 
  font-weight: bolder;
}
.SuccessPage {
  min-width: 100vw;
  min-height: 100vh;
  background-color: #e5e5e5;
  position: relative;
}

.SuccessBox {
  position: absolute;
  width: 488px;
  height: 358px;
  background-color: white;
  border-radius: 15.85px;
  top: 20%;
  left: 35%;
}

.Container {
  text-align: center;
  margin-top: 30px;
}

.text {
  font-size: 16px;
  line-height: 21px;
  font-weight: bold;
  margin-top: 20px;
}

.doneBtn {
  background:#230480;
  border-radius: 6px;
  width: min(30%, 30%);
  height: 51px;
  border: none;
  color: #fff;
  font-size: 18px;
  margin-top: 10px;
  cursor: pointer;
}

.doneBtn:hover {
  opacity: 0.8;
}

.doneBtn:focus {
  outline: none;
  opacity: 0.75;
}
